.about {
  width: 100vw;
  height: 80vh;
  background-color: $bg;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 30px;
}

.aboutText {
  @include centralize;
  padding-top: 80px;
  padding: 40px;
  letter-spacing: -0.6px;
  flex-direction: column;
  font-size: 1.6rem;
  text-align: center;
  font-weight: normal;
  color: $text;
  width: 50%;
  line-height: 54px;

  &:after {
    content: '';
    display: block;
    margin: 20px 0 0 300px;
    background-image: url("./../../assets/Signature.svg");
    background-size: cover;
    width: 200px;
    height: 140px;
  }
}

div .aboutText {
  height: 700px;
  padding-top: 90px;
  background: $bg;
}

.aboutImage {
  background-image: url("./../../assets/People.jpg");
  background-size: cover;
  width: 50%;
  height: 700px;
}
